<template>
	<view>
		<u-sticky>
			<u-tabs :list="tab_list" :scrollable="false" @change="change"> </u-tabs>
		</u-sticky>

		<uni-list v-if="cur==0">
			<uni-list-item v-for="(item,index) in options.raceList" :title="item.name" :note="item.displayName" link
				:key="item.TFTID" :to="'../fettersDetail/fettersDetail?race_id='+item.raceId"
				:thumb="'https://game.gtimg.cn/images/lol/act/img/tft/origins/'+item.alias" thumbSize="lg">
				{{item.name}}
			</uni-list-item>
		</uni-list>
		<uni-list v-if="cur==1">
			<uni-list-item v-for="(item,index) in options.jobList" :key="item.TFTID" :title="item.name"
				:note="item.displayName" link :to="'../fettersDetail/fettersDetail?job_id='+item.jobId"
				:thumb="item.imagePath" thumbSize="lg">
				{{item.name}}
			</uni-list-item>
		</uni-list>
	</view>
</template>

<script>
	import {
		options
	} from '@/common/options.js'
	export default {
		data() {
			return {
				tab_list: [{
						name: '种族'
					},
					{
						name: '职业',
					},

				],
				options: {},
				//tabs的当前索引
				cur: 0,
			};
		},
		methods: {
			change(item) {
				this.cur = item.index
			}
		},
		computed: {

		},
		onLoad() {
			function deepClone(obj) {
				let copy = obj instanceof Array ? [] : {}
				for (let key in obj) {
					if (obj.hasOwnProperty(key)) {
						copy[key] = typeof obj[key] === 'object' ? deepClone(obj[key]) : obj[key]
					}
				}
				return copy
			}
			this.options = deepClone(options)
		}
	}
</script>

<style lang="scss">
	.u-tabs {
		background-color: #f8f8f8;
	}
</style>
